.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-container {
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card {
    background: #1a1919;
    padding: 1.2rem 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.project-card img {
    width: 100%;
    height: 140px;
}

.project-title {
    color: white;
    padding: 1rem;
    text-align: center;
}

.pro-details p {
    padding-bottom: 1rem;
    font-size: 1.1rem;
    text-align: justify;
    }

.pro-btns{

display: flex;
justify-content: center;   
margin-top: auto;

}    

@media screen and (max-width: 740px) {
    .project-container {
    max-width: 90%;
    margin: auto; 
    grid-template-columns: 1fr;
    }
}